<template>
    <div id="signIn">
        <el-container style="height:100%;">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span></span>
                </div>
                <el-form ref="form" :model="form" :rules="rules" status-icon label-width="80px" class="demo-ruleForm">
                    <el-form-item label="" prop="username">
                        <el-input v-model="form.username" clearable></el-input>
                    </el-form-item>
                    <el-form-item label=" " prop="password">
                        <el-input type="password" v-model="form.password" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item style="padding-left:19%;">
                        <el-button @click="resetForm('form')"></el-button>
                        <el-button type="primary" @click="loading"> </el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </el-container>
    </div>
</template>

<script>
    import tools from './lib/tools'
    export default {
        name: 'signIn',
        data () {
            // var validatePass = (rule, value, callback) => {
            //     console.log(value)
            //     if (value === '') {
            //         callback(new Error(''))
            //     } else {
            //         callback()
            //     }
            // }
            return {
                form: {
                    username: '',
                    password: ''
                },
                rules: {
                    username: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    password: [
                        // { validator: validatePass, trigger: 'blur' }
                        { required: true, message: '', trigger: 'blur' },
                        { min: 6, max: 18, message: '6-18', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            resetForm (formName) {
                this.$refs[formName].resetFields()
            },
            loading () {
                let _this = this
                this.$http.axios({method: 'POST', url: '/api/signIn', data: {username: _this.form.username, password: _this.form.password}}).then((result) => {
                    if (result && result.data) {
                        let data = result.data
                        if (data.error_code === 0 && data.message === 'SUCCESS') {
                            tools.setCookie('username', data.result.username)
                            tools.setCookie('_id', data.result._id)
                            tools.next('/')
                        } else {
                            _this.$message.error(data.message, data.error_code)
                        }
                    } else {
                        console.error(result)
                        _this.$message.error('')
                    }
                }).catch((error) => {
                    console.log('error')
                    console.error(error)
                })
                // this.$message.error('')
            }
        }
    }
</script>

<style src="../static/css/reset.css" ></style>

<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 480px;
        height: 230px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }

</style>



